/* stylelint-disable no-descending-specificity */
.van-steps {
  overflow: hidden;
  background-color: #fff;
  background-color: var(--steps-background-color, #fff);
}
.van-steps--horizontal {
  padding: 0.42667rem;
}
.van-steps--horizontal .van-step__wrapper {
  position: relative;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
}
.van-steps--vertical {
  padding-left: 0.42667rem;
}
.van-steps--vertical .van-step__wrapper {
  padding: 0 0 0 0.85333rem;
}
.van-step {
  position: relative;
  -webkit-flex: 1;
          flex: 1;
  font-size: 0.59733rem;
  font-size: var(--step-font-size, 0.59733rem);
  color: #969799;
  color: var(--step-text-color, #969799);
}
.van-step--finish {
  color: #323233;
  color: var(--step-finish-text-color, #323233);
}
.van-step__circle {
  border-radius: 50%;
  width: 0.21333rem;
  width: var(--step-circle-size, 0.21333rem);
  height: 0.21333rem;
  height: var(--step-circle-size, 0.21333rem);
  background-color: #969799;
  background-color: var(--step-circle-color, #969799);
}
.van-step--horizontal {
  padding-bottom: 0.59733rem;
}
.van-step--horizontal:first-child .van-step__title {
  -webkit-transform: none;
          transform: none;
}
.van-step--horizontal:first-child .van-step__circle-container {
  padding: 0 0.34133rem 0 0;
  -webkit-transform: translate3d(0, 50%, 0);
          transform: translate3d(0, 50%, 0);
}
.van-step--horizontal:last-child {
  position: absolute;
  right: 0;
  width: auto;
}
.van-step--horizontal:last-child .van-step__title {
  text-align: right;
  -webkit-transform: none;
          transform: none;
}
.van-step--horizontal:last-child .van-step__circle-container {
  right: 0;
  padding: 0 0 0 0.34133rem;
  -webkit-transform: translate3d(0, 50%, 0);
          transform: translate3d(0, 50%, 0);
}
.van-step--horizontal .van-step__circle-container {
  position: absolute;
  bottom: 0.256rem;
  z-index: 1;
  -webkit-transform: translate3d(-50%, 50%, 0);
          transform: translate3d(-50%, 50%, 0);
  background-color: #fff;
  background-color: var(--white, #fff);
  padding: 0 0.34133rem;
  padding: 0 var(--padding-xs, 0.34133rem);
}
.van-step--horizontal .van-step__title {
  display: inline-block;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  font-size: 0.512rem;
  font-size: var(--step-horizontal-title-font-size, 0.512rem);
}
.van-step--horizontal .van-step__line {
  position: absolute;
  right: 0;
  bottom: 0.256rem;
  left: 0;
  height: 0.04267rem;
  -webkit-transform: translate3d(0, 50%, 0);
          transform: translate3d(0, 50%, 0);
  background-color: #ebedf0;
  background-color: var(--step-line-color, #ebedf0);
}
.van-step--horizontal.van-step--process {
  color: #323233;
  color: var(--step-process-text-color, #323233);
}
.van-step--horizontal.van-step--process .van-step__icon {
  display: block;
  line-height: 1;
  font-size: 0.512rem;
  font-size: var(--step-icon-size, 0.512rem);
}
.van-step--vertical {
  padding: 0.42667rem 0.42667rem 0.42667rem 0;
  line-height: 0.768rem;
}
.van-step--vertical::after {
  border-bottom-width: 0.04267rem;
}
.van-step--vertical:last-child::after {
  border-bottom-width: none;
}
.van-step--vertical:first-child::before {
  position: absolute;
  top: 0;
  left: -0.64rem;
  z-index: 1;
  width: 0.04267rem;
  height: 0.85333rem;
  content: '';
  background-color: #fff;
  background-color: var(--white, #fff);
}
.van-step--vertical .van-step__icon,
.van-step--vertical .van-step__circle,
.van-step--vertical .van-step__line {
  position: absolute;
  top: 0.81067rem;
  left: -0.59733rem;
  z-index: 2;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}
.van-step--vertical .van-step__icon {
  line-height: 1;
  font-size: 0.512rem;
  font-size: var(--step-icon-size, 0.512rem);
}
.van-step--vertical .van-step__line {
  z-index: 1;
  width: 0.04267rem;
  height: 100%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  background-color: #ebedf0;
  background-color: var(--step-line-color, #ebedf0);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
